
type Feature =
  | { name: 'SendTransaction'; maxMessages: number } // `maxMessages`  `SendTransaction` 
  | { name: 'SignData' }
interface TonConnectBridge {
  deviceInfo: DeviceInfo // /
  walletInfo?: any
  protocolVersion: number //  Ton Connect  2
  isWalletBrowser: boolean // 
  connect(protocolVersion: number, message: ConnectRequest): Promise<ConnectEvent>
  restoreConnection(): Promise<ConnectEvent>
  send(message: AppRequest): Promise<WalletResponse>
  listen(callback: (event: WalletEvent) => void): () => void
}

type DeviceInfo = {
  platform: 'iphone' | 'ipad' | 'android' | 'windows' | 'mac' | 'linux'
  appName: string //  "Tonkeeper"
  appVersion: string //  "2.3.367"
  maxProtocolVersion: number
  features: Feature[] // RPC 
  //  -- 'SendTransaction';
}
type TonAddressItem = {
  name: 'ton_addr'
}
type TonProofItem = {
  name: 'ton_proof'
  payload: string //  nonce + 
}
type ConnectItem = TonAddressItem | TonProofItem
type ConnectRequest = {
  manifestUrl: string
  items: ConnectItem[] // 
}

type ConnectEvent = ConnectEventSuccess | ConnectEventError
type ConnectItemReply = TonAddressItemReply | TonProofItemReply
// 
//  ton_proof
type TonAddressItemReply = {
  name: 'ton_addr'
  address: string // TON  (`0:<hex>`)
  network: NETWORK //  global_id
  publicKey: string // HEX  0x
  walletStateInit: string // Base64 URL stateinit cell
}

type TonProofItemReply = TonProofItemReplySuccess | TonProofItemReplyError

type TonProofItemReplySuccess = {
  name: 'ton_proof'
  proof: {
    timestamp: string //  64  unix epoch 
    domain: {
      lengthBytes: number // AppDomain 
      value: string //  url 
    }
    signature: string // base64 
    payload: string // 
  }
}

type TonProofItemReplyError = {
  name: 'ton_addr'
  error: {
    code: 1 // TODO: ConnectItemErrorCode
    message?: string
  }
}

enum NETWORK {
  MAINNET = '-239',
  TESTNET = '-3',
}
type ConnectEventSuccess = {
  event: 'connect'
  id: number // 
  payload: {
    items: ConnectItemReply[]
    device: DeviceInfo
  }
}
type ConnectEventError = {
  event: 'connect_error'
  id: number // 
  payload: {
    code: number
    message: string
  }
}

interface AppRequest {
  method: string // 'sendTransaction', 'signMessage', ...
  params: string[]
  id: string
}

type WalletResponse = WalletResponseSuccess | WalletResponseError

interface WalletResponseSuccess {
  result: string
  id: string
}

interface WalletResponseError {
  error: { code: number; message: string; data?: unknown }
  id: string
}

interface WalletEvent {
  event: WalletEventName
  id: number // 
  payload: any
  // payload: <event-payload>; // 
}

type WalletEventName = 'connect' | 'connect_error' | 'disconnect'

